<template>
    <div class="mainc">
        <div class="bar-nav">
            <button class="bar-btn left-bar-btn bar-back"></button>
            <button class="bar-btn right-bar-btn">编辑</button>
            <span class="bar-title">购物车</span>
        </div>

        <div class="cart-content">
            <div class="cart-empty" style="display: none">
                <div class="cart-moov"><img src="https://thl.lecuntao.com/youxuan/icons/shopping.png" class="empty-img">
                </div>
                <span class="empty-text">还没有添加商品...</span>
                <button class="empty-btn">去购物</button>
            </div>

            <div class="cart-item">
                <div class="goods-img-content">
                    <img src="https://thl.lecuntao.com/uploads/goods/20190124142733597.png" class="item-img">
                </div>
                <div class="cartItem-msg">
                    <div class="goods-name">【秒杀】生活卫生纸960g*16卷LX16960</div>
                    <div class="num">960g*16卷</div>
                    <div class="price">￥
                        <span class="price-color">31:00</span>
                    </div>
                </div>
                <div class="num-contents">
                    <button class="minus">-</button>
                    <span class="number"></span>
                    <button class="add">+</button>
                </div>
                <span class="choice-icon checked-off" data-key=""></span>
            </div>
        </div>
        <div class="btm-msgs">

            <div class="left-msg">
                <div class="choice-all checked-off">全选</div>
                <div class="heji">合计:
                    <text class="heji-icon">￥</text>
                </div>
                <span class="price">311</span>
            </div>
            <button class="right-btn">结算(6)</button>
        </div>
    </div>
</template>
<script>
    import "../../assets/format"
    export default {
        name: "cart",
        data(){
            return {
                config: this.GLOBAL.config,
                shopid: "",
                edit: false,
                isempty: -1,
                emptyImg: this.GLOBAL.config + '/youxuan/icons/shopping.png',
                isAll: true,
                sumprice: 0,
                sumNum: 0,
                cartList: [],
                site: ""
            }
        },
        created: function () {
            this.shopid = this.GLOBAL.userMsg.store_id;
            this.getData()
        },
        mounted() {

        },

        methods: {
            getData:function () {
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                this.axios.get('/minapi/cart/getCartList', {
                    params: {
                        area_id: this.GLOBAL.shopMsg.area_id || '',
                        role: this.GLOBAL.role,
                        access_token: this.GLOBAL.userMsg.access_token
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (datas.error_code == 0) {
                        var cartList = [];
                        for (var i = 0, len = datas.data.length; i < len; i++) {
                            var obj = datas.data[i];
                            obj.max = obj.stock == 999999999 ? 999999999 : obj.stock - obj.sale;
                            obj.checked = (obj.max > 0);
                            cartList.push(obj)
                        }

                        this.cartList=cartList;
                        this.isempty=cartList.length;
                    } else {
                        this.isempty=0;
                    }
//                    this.count()
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: ' 请求错误：getCartList',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },

        },
        watch:{

        },
        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_cart.css);
</style>