<template>
    <div class="mainc">
        <div class="login-page">
            <div class="forget-input">
                <div class="input-item">
                    <span>手机号：</span>
                    <input type="number" placeholder="请输入您的手机号" data-value="phone" v-model="phone">
                </div>
                <div class="input-item">
                    <span>验证码：</span>
                    <input type="text" placeholder="请输入验证码" data-value="codes" v-model="codes">
                </div>
                <div class="input-item">
                    <span>新登录密码：</span>
                    <input type="password" password="true" placeholder="请输入新密码" data-value="newpw" v-model="newpw">
                </div>
                <div class="input-item">
                    <span>确认密码：</span>
                    <input type="password" password="true" placeholder="请输入确认密码" data-value="twopw" v-model="twopw">
                </div>
                <div class="send-content">
                    <button class="send-btn" @click="getCodes" :disabled="isdisabled">{{isdisabled?times+'秒重新发送':'获取验证码'}}</button>
                </div>
            </div>
            <button class="submit-btn" @click="submitData">提交</button>
        </div>
    </div>
</template>
<script>
    export default {
        name: "forget",
        data(){
            return {
                config: this.GLOBAL.config,
                types: '', //1忘记密码 2修改密码
                phone: '',
                upPhone: '',
                codes: '',
                newpw: '',
                twopw: '',
                isdisabled: false,
                times: 60,
                shopMsg: null,
                userMsg: null
            }
        },
        created: function () {
            var type = this.$route.query.type;
            var mobile = '';
            if (type == 2) {
                mobile = (this.GLOBAL.userMsg.phone + '').replace(/(\d{3})\d+(\d{4})/, '$1****$2');
            }
            this.types = type || 1;
            this.shopMsg = this.GLOBAL.shopMsg || {};
            this.phone = mobile;
            this.upPhone = this.GLOBAL.userMsg && this.GLOBAL.userMsg.phone ? this.GLOBAL.userMsg.phone : ''
        },
        mounted() {

        },

        methods: {
            getCodes: function () {
                var reg = new RegExp('^1[0-9]{10}$');
                if (!reg.test(this.phone)) {
                    this.toast = this.$createToast({
                        txt: '请输入正确手机号',
                        time: 1000,
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                if (this.isdisabled) {
                    return
                }
                this.isdisabled = true;

                var outtime = setInterval(() => {
                    var times = this.times - 1;
                    this.times = times;
                    if (times <= 0) {
                        clearInterval(outtime);
                        this.isdisabled = false;
                        this.times = 60;
                    }
                }, 1000);

                this.axios.get('/minapi/login/sendCode', {
                    params: {
                        mobile: this.phone
                    }
                }).then(res => {
                    var datas = res.data;
                    if (datas.error_code == 0) {
                        this.toast = this.$createToast({
                            txt: '验证码已发送成功，15分钟内有效，请注意查收',
                            time: 1000,
                            type: 'txt'
                        })
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: ' 请求错误：sendCode',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            /** 提交数据 **/
            submitData: function() {
                var reg = new RegExp('^1[3|4|5|6|7|8][0-9]{9}$');
                var phone = this.types == 1 ? this.phone : this.upPhone;
                if (!reg.test(phone)) {
                    this.toast = this.$createToast({
                        txt: '请输入正确手机号',
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                if (this.codes == '') {
                    this.toast = this.$createToast({
                        txt:'请输入验证码',
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                if (this.newpw == '') {
                    this.toast = this.$createToast({
                        txt:'请输入新密码',
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                if (this.newpw.length < 6) {
                    this.toast = this.$createToast({
                        txt:'密码不得少于6位数',
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                if (this.twopw != this.newpw) {
                    this.toast = this.$createToast({
                        txt:'两次密码不一致',
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show();
                this.axios.get('/minstore/login/forgetPassword', {
                    params: {
                        mobile: phone,
                        code: this.codes,
                        password: this.newpw,
                        repassword: this.twopw
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (datas.error_code == 0) {
                        this.$createDialog({
                            type: 'alert',
                            title: '操作成功',
                            content: '修改密码成功',
                            onConfirm: () => {
                                this.$router.back(-1);
                            }
                        }).show()
                    } else {
                        this.toast = this.$createToast({
                            txt: '操作失败：' + datas.error_msg,
                            type: 'txt'
                        })
                        this.toast.show();
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: '请求错误：forgetPassword',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
        },
        watch:{

        },
        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_forget.css);
</style>