<template>
    <div class="mainc">
        <div class="order-nav" :data-index="index">
            <div class="orderNav-item" :class="[index==1?'active':'']" @click="changeItem" data-index="1">全部</div>
            <div class="orderNav-item" :class="[index==2?'active':'']" @click="changeItem" data-index="2">未付款</div>
            <div class="orderNav-item" :class="[index==3?'active':'']" @click="changeItem" data-index="3">待提货</div>
            <div class="orderNav-item" :class="[index==4?'active':'']" @click="changeItem" data-index="4">已提货</div>
        </div>

        <div class="order-content">
            <div class="order-item" :class="[item.hide?'hidden':'']" v-for="(item,i) in orderList" :data-index="i" v-if="(index==1&&item.order_status!=10)||(item.order_status==0&&index==2)||(item.order_status==2&&index==3)||(item.order_status==3&&index==4)">
                <div class="item-top">
                    <span class="right-status" :class="[item.order_status==0?'active':'']">{{item.order_status==0?"未付款":item.order_status==1?"待发货":item.order_status==2?"待提货":item.order_status==3?"已提货":"交易关闭"}}</span>
                    <span class="qr-right" v-if="item.order_status==2" @click="previewImg" :data-url="item.get_code"></span>
                    <span class="order-sn">订单编号：{{item.order_sn}}</span>
                </div>
                <router-link tag="div" class="item-middle" :to="{path:'orderdet', query: { oid: item.order_id }}" :data-index="i">
                    <img :src="config+goods.banner" alt="" v-for="goods in item.goods" class="banner-img">
                </router-link>
                <div class="item-times">
                    <div class="right-price">共{{item.goods_num}}件商品
                        <span class="text">￥{{item.order_account-0}}</span>
                    </div>
                    <span class="left-time">{{item.create_time}}</span>
                </div>
                <div class="item-bottom">
                    <router-link tag="button" class="right-btn" :class="[item.order_status==0?'active':'']"  v-if="item.order_status==0" @click.native="viewTap" :data-index="i" :to="{path:'payment', query: { type: 2, oid:item.order_id ,price:item.order_account, order:item.order_sn}}">去付款</router-link>
                    <button class="right-btn" v-if="item.order_status!=2&&item.order_status!=1&&item.order_status!=0" :data-index="i" @click="deleteOrder">删除</button>
                    <div class="order-num">提货单号：
                        <span class="text">{{item.get_num}}</span>
                        <span class="phone">{{item.replace_phone}}</span>
                    </div>
                </div>
            </div>
            <div class="btm-nomore">{{nomore==1?'加载中···':nomore==3?'没有更多记录了···':' '}}</div>
        </div>
    </div>
</template>
<script>
    import "../../assets/format"
    export default {
         name:'order1',
        data(){
            return {
                config: this.GLOBAL.config,
                index: 1,
                nomore: 2, //1加载中 2加载完成 3没数据了
                page: 1,
                orderList: []
            }
        },

        created: function () {
            var index = this.$route.query.index;
            this.index=index;

            this.getData();
        },
        methods: {
            changeItem:function (e) {
                var ind = e.target.dataset.index;
                if (ind != this.index) {
                    this.index=ind;
                    this.getData();
                }
            },
            previewImg:function (e) {
                var urls = this.config + e.target.dataset.url; //图片下标
                window.open(urls);
            },
            getData:function () {
                var userMsg = this.GLOBAL.userMsg;
                var obj = this;
                if (obj.nomore != 2) {
                    return
                }
                this.nomore=1;
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                this.axios.get('/minapi/orders/OrderList', {
                    params: {
                        access_token: userMsg.access_token
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (obj.page == 1) {
                        this.orderList=[];
                    }
                    if (datas.error_code == 0) {
                        var orderList = this.orderList;
                        var datalist = datas.data;
                        for (var i = 0, len = datalist.length; i < len; i++) {
                            datalist[i].create_time = new Date(datalist[i].create_time * 1000).format('yyyy-MM-dd hh:mm:ss');
                            // datalist[i].replace_phone = datalist[i].replace_phone.replace(/^(.{3})(.{4})(.+)/ig,'$1****$3');
                        }
                        orderList = orderList.concat(datalist);
                        this.orderList=orderList;
                        this.nomore=3;
                    } else {
                        this.nomore=3;
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: '请求错误：OrderList',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            viewTap:function (e) {
                var index = e.target.dataset.index;
                localStorage.setItem('replacename', this.orderList[index].replace_name);
            },
            deleteOrder:function (e) {
                var keys = e.target.dataset.index;
                var orderList = this.orderList;
                var userMsg = this.GLOBAL.userMsg;
                this.$createDialog({
                    type: 'confirm',
                    title: '删除订单',
                    content: '确认删除该订单？',
                    onConfirm: () => {
                        const pop = this.$createToast({
                            time: 10000,
                        });
                        pop.show();
                        this.axios.get('/minapi/orders/delorder', {
                            params: {
                                order_id: orderList[keys].order_id,
                                access_token: userMsg? userMsg.access_token : ''
                            }
                        }).then(res => {
                            var datas = res.data;
                            var info = datas.error_msg;
                            pop.hide();
                            if (datas.error_code == 0) {

                                this.info = this.$createToast({
                                    txt: '删除成功',
                                    type: 'txt'
                                })
                                this.info.show();
                                orderList[keys].hide = true;
                                this.orderList = orderList.filter((item)=>{
                                  return item.hide != true;
                                });
                            } else {
                                this.info = this.$createToast({
                                    txt: '操作失败'+info,
                                    type: 'txt'
                                })
                                this.info.show();
                            }
                        }).catch(error => {
                            this.toast = this.$createToast({
                                txt: '请求错误：delorder',
                                type: 'txt'
                            })
                            this.toast.show();
                        })
                    }
                }).show()
            }
        },
    }
</script>

<style scoped>
    @import url(./static/css/d_order1.css);
</style>

