<template>
    <div class="mainc">
        <div class="login-content" :class="[moveIn?'moveIn':'']">
            <div class="logo-content">
                <img src="https://thl.lecuntao.com/youxuan/icons/logo.png" class="logo-img">
            </div>

            <div class="s_radio-group">
                <ul>
                    <li class="s_radio" v-for="(item, i) in items" :class="{active:i==n}" @click="radioChange(item,i)">
                        <i></i>
                        {{item.value}}
                    </li>
                </ul>
            </div>

            <div class="input-content">
                <input type="number" placeholder="请输入您的手机号" class="userPhone" v-model="phone">
                <i></i>
            </div>
            <div class="input-content" v-if="types==1">
                <input type="text" placeholder="请输入验证码" class="userCode" v-model="codes">
                <button type="button" class="code-btn" @click="getCodes" :disabled="isdisabled">
                    {{isdisabled ? times + '秒重新发送' : '获取验证码'}}
                </button>
            </div>
            <div class="input-content" v-if="types==2">
                <input type="text" password="true" placeholder="请输入密码" class="userCode" v-model="codes">
            </div>
            <button class="login-btn eo" v-if="authSetting" @click="submitLogin">登录</button>
            <button class="login-btn" v-if="!authSetting" data-type="1" @click="submitLogin">登录</button>
            <div class="other-forget" v-if="types==2">
                <router-link tag="span" :to="{path:'forget', query: { type: '1' }}" class="right-text">忘记密码?</router-link>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: "login",
        data(){
            return {
                config: this.GLOBAL.config,
                authSetting: false,
                logoImg: this.GLOBAL.config + '/youxuan/icons/logo.png',
                types: 1, //角色
                phone: "",
                codes: "",
                moveIn: true,
                isdisabled: false,
                times: 60,
                items: [{
                    name: '1',
                    value: '用户',
                }, {
                    name: '2',
                    value: '商家'
                }],
                n: 0
            }
        },
        methods: {
//            模拟数据
        /*    login: function () {
                const saveData = {
                    "uid": 67,
                    "username": null,
                    "mobile": "13834600941",
                    "password": null,
                    "openid": "o6Tip5UebqnispQRgNrNIkeUnRXA",
                    "nickname": "袁先生",
                    "face": "https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eosxvQRgCzjpYsjdIbodvmE1zkcZ5qPtAx0lezt0Bm8lMVl2L04pF93a867uiaZiblEGVBCDMoibRPUg/132",
                    "role": "customer",
                    "create_time": "2018-11-27 15:44:09",
                    "last_time": 1550906253,
                    "store_id": 10,
                    "com_id": 1,
                    "remarks": null,
                    "ban": 0,
                    "is_login": "1",
                    "area_id": null,
                    "pv": 22,
                    "sex": 0,
                    "access_token": "8fa576dd0ca422a8dd8954ba01740b12"
                };
                const shopMsg = {
                    "store_id": 241,
                    "store_sn": "00000241",
                    "store_name": "太原小店区真武路汇泰丽城",
                    "store_face": "/uploads/store/93aebe74455e8d6886315e3f919c0f2f.jpg",
                    "fen": "11",
                    "buy": "37",
                    "province": "山西省",
                    "city": "太原市",
                    "area": "小店区",
                    "area_desc": "小店区真武路与正阳街交叉口向东50米汇泰丽城二号楼二单元17层1701号",
                    "server_time": 1550453433,
                    "add_phone": "18635101203",
                    "supply_phone": "13546444694"
                };    //注意---商铺信息本应该在进入小程序是获取
                var role = 1;
                var refresh = 1;
                this.GLOBAL.shopMsg = shopMsg;              //注意---商铺信息本应该在进入小程序是获取
                this.GLOBAL.userMsg = saveData;
                this.GLOBAL.role = role;
                localStorage.setItem('userMsg', JSON.stringify(saveData));
                localStorage.setItem('shopMsg', JSON.stringify(shopMsg)); //注意---商铺信息本应该在进入小程序是获取
                localStorage.setItem('role', role);
                localStorage.setItem('refresh', refresh);
                this.toast = this.$createToast({
                    txt: '已登录',
                    time: 1000,
                    type: 'txt'
                })
                this.toast.show()
                setTimeout(() => {
                    this.$router.push({name: 'index'});
                }, 1000)
            },*/
            radioChange: function (item, i) {
                this.n = i;
                this.types = item.name;
                this.codes = '';

            },
            getCodes: function () {
                var reg = new RegExp('^1[0-9]{10}$');
                if (!reg.test(this.phone)) {
                    this.toast = this.$createToast({
                        txt: '请输入正确手机号',
                        time: 1000,
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                if (this.isdisabled) {
                    return
                }
                this.isdisabled = true;

                var outtime = setInterval(() => {
                    var times = this.times - 1;
                    this.times = times;
                    if (times <= 0) {
                        clearInterval(outtime);
                        this.isdisabled = false;
                        this.times = 60;
                    }
                }, 1000);

                this.axios.get('/minapi/login/sendCode', {
                    params: {
                        mobile: this.phone
                    }
                }).then(res => {
                    var datas = res.data;
                    if (datas.error_code == 0) {
                        this.toast = this.$createToast({
                            txt: '验证码已发送成功，15分钟内有效，请注意查收',
                            time: 1000,
                            type: 'txt'
                        })
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: ' 请求错误：sendCode',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            submitLogin: function () {
                var role = this.types;
                var loginUrl = '';
                var data = {
                    mobile: this.phone,
                    nickname: this.GLOBAL.userInfo.nickName,
                    face: this.GLOBAL.userInfo.avatarUrl
                };
                if (role == 1) {
                    loginUrl = '/minapi/login/userLogin';
                    data.code = this.codes;
                } else if (role == 2) {
                    loginUrl = '/minstore/login/storeLogin';
                    data.password = this.codes;
                }
                if (loginUrl == '') {
                    return
                }
                var reg = new RegExp('^1[3|4|5|6|7|8][0-9]{9}$')
                if (!reg.test(data.mobile)) {
                    this.toast = this.$createToast({
                        txt: '请输入正确手机号',
                        time: 1000,
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                if (role == 1 && data.code == '') {
                    this.toast = this.$createToast({
                        txt: '请输入验证码',
                        time: 1000,
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                } else if (role == 2 && data.password == '') {
                    this.toast = this.$createToast({
                        txt: '请输入密码',
                        time: 1000,
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }
                data.openid = this.GLOBAL.session.openid;
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show();

                if (!data.openid) {
                    data.openid = this.GLOBAL.session.openid;
                } else {
                    this.login(data, loginUrl, role);
                }
            },
            login:function (dat, loginUrl, role) {
               //做相对应头像判断
                /*                if (!dat.face || !dat.nickname) {
                    this.toast = this.$createToast({
                        txt: '未获取微信信息，请重启再尝试登录',
                        time: 1000,
                        type: 'txt'
                    })
                    this.toast.show();
                    return
                }*/

                const shopMsg = {
                    "store_id": 241,
                    "store_sn": "00000241",
                    "store_name": "太原小店区真武路汇泰丽城",
                    "store_face": "/uploads/store/93aebe74455e8d6886315e3f919c0f2f.jpg",
                    "fen": "11",
                    "buy": "37",
                    "province": "山西省",
                    "city": "太原市",
                    "area": "小店区",
                    "area_desc": "小店区真武路与正阳街交叉口向东50米汇泰丽城二号楼二单元17层1701号",
                    "server_time": 1550453433,
                    "add_phone": "18635101203",
                    "supply_phone": "13546444694"
                };    //注意---商铺信息本应该在进入小程序是获取
                this.GLOBAL.shopMsg = shopMsg;
                localStorage.setItem('shopMsg', JSON.stringify(shopMsg)); //注意---商铺信息本应该在进入小程序是获取

                const pop = this.$createToast({
                        time: 10000,
                    });
                    pop.show()
                    this.axios.get(loginUrl, {
                        params: dat
                    }).then(res => {
                        var datas = res.data;
                        pop.hide();
                        if (datas.error_code == 0) {
                            localStorage.setItem('userMsg', JSON.stringify(datas.data));
                            localStorage.setItem('role', role);
                           this.GLOBAL.role = this.types;
                            this.GLOBAL.userMsg = datas.data;
                            if (role == 1) {
                                localStorage.setItem('refresh', 1);
                            }
                            this.toast = this.$createToast({
                                txt: ' 登录成功',
                                type: 'txt',
                                time:1000
                            })
                            this.toast.show();
                            setTimeout(() => {
                              this.$router.back(-1);
                            }, 1000)
                        } else {
                            this.toast = this.$createToast({
                                txt: '操作失败：' + datas.error_msg,
                                type: 'txt'
                            })
                            this.toast.show();
                        }
                    }).catch(error => {
                        this.toast = this.$createToast({
                            txt: '请求错误：' + loginUrl.replace(/.+\//ig, ''),
                            type: 'txt'
                        })
                        this.toast.show();
                    })
            }
        }
    }
</script>
<style scoped>
    @import url(./static/css/d_login.css);
</style>