<template>
    <div class="mainc">
        <div class="coupon-container"  ref="bscroll">
            <div class="coupon-list bscroll-container">
                <div class="coupon-item" @click="choiceCoupon(item )" v-for="(item,i) in list"  :class="[item.use_state?'':'overdue-item']">
                    <div class="coupon-left" :class="[item.type==1?'new-item':'']">
                        <div class="text-top">现金券</div>
                        <div class="text-middle">
                            <span>￥</span>
                            <span class="price-text">{{item.money-0}}</span>
                        </div>
                        <div class="text-bottom">{{item.use_state?'可使用':'已过期'}}</div>
                    </div>
                    <div class="coupon-right">
                        <div class="text-title">{{item.type==1?"平台通用新人专享券":item.condition_money==0?"通用红包":"满"+(item.condition_money-0)+"元可用 "+(item.money-0)+"元"}}</div>
                        <div class="coupon-type">{{item.type==1?"新人券":"商家券"}}</div>
                        <div class="coupon-time">使用期限{{item.send_time}}-{{item.expired_time}}</div>
                         <span class="checked-icon" :class="[choiceId==item.id?'active':'']" v-if="types==2"></span>
                    </div>
                </div>
                <div class="btm-nomore" v-if="nomore!=2">{{nomore==1?'加载中···':nomore==3?'没有更多记录了···':' '}}</div>
            </div>
        </div>
    </div>

</template>
<script>
    import "../../assets/format"
    import BScroll from 'better-scroll'
    export default {
        name: "coupon",
        data(){
            return {
                config: this.GLOBAL.config,
                types: "", //1为浏览 2为选择优惠券
                condition: 0, //选择优惠券条件 满减
                choiceId: "", //选中红包的id
                nomore: 2, //1加载中 2加载完成 3没数据了
                page: 1,
                list: []
            }
        },
        created: function () {
            this.getData();
        },
        mounted() {
            setTimeout(()=>{
                this.initScroll();
            },20)
        },

        methods: {
            getData: function () {
                var obj = this;
                if (obj.nomore != 2) {
                    return
                }
                this.nomore = 1;

                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                this.axios.get('/minapi/Coupon/Couponlist', {
                    params: {
                        access_token: this.GLOBAL.userMsg.access_token,
                        num: 10,
                        page: this.page
                    }
                }).then(res => {
                    var datas = res.data;

                    if (obj.page == 1) {
                        pop.hide();
                        this.list=[];
                    }
                    if (datas.error_code == 0) {
                        var list = this.list;
                        list = list.concat(datas.data);
                        this.list = list;
                        if (datas.data.length < 10) {
                            this.nomore = 3;
                        } else {
                            this.nomore = 2;
                            this.page = this.page +1;
                        }
                    } else {
                        this.nomore = 3;
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: ' 请求错误：storeUsers',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            choiceCoupon:function (e) {
                if (this.types != 2) {
                    return
                }
                var dataset = e;
                console.log(dataset);
                if (dataset.use_state) {
                    if (dataset.condition_money-0 <= this.condition) {
                        this.$createDialog({
                            type: 'confirm',
                            title: '选择优惠券',
                            content: '确认使用该优惠券？',
                            onConfirm: () => {
                                const coupon = { id: dataset.id, money: dataset.money-0}
                                localStorage.setItem('coupon', JSON.stringify(coupon));
                                this.choiceId = dataset.id;
                                this.$router.back(-1);
                            }
                        }).show()
                    } else {
                        this.toast = this.$createToast({
                            txt: '该订单金额未满足优惠券使用条件',
                            type: 'txt'
                        })
                        this.toast.show();
                    }
                } else {
                    this.toast = this.$createToast({
                        txt: '该优惠券已过期',
                        type: 'txt'
                    })
                    this.toast.show();
                }
            },
            initScroll(){
                if(!this.$refs.bscroll){
                    return ;
                }
                this.scroll = new BScroll(this.$refs.bscroll,{
                    click:true,
                    scrollbar:true,
                    pullUpLoad: {
                        threshold: 50
                    }
                });
                this.scroll.on('pullingUp',()=>{
                    this.getData();
                })
            },
        },
        watch: {
            list:{
                handler:function(){
                    this.$nextTick(()=>{
                        if(this.scroll) {
                            this.scroll.finishPullUp();
                            this.scroll.refresh();
                        }
                    })
                },
                deep:true
            }
        },
        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_coupon.css);
</style>