<template>
    <div class="mainc">
        <div class="foot-content" v-if="status!=2&&status!=1">
            <router-link tag="button" class="od-btn active" v-if="status==0"
                         :to="{path:'payment', query: { type: 3, oid:orderMsg.order_id, price:orderMsg.order_account, order:orderMsg.order_sn  }}">
                去付款
            </router-link>
            <button class="od-btn" v-if="status!=0" @click="deleteOrder">删除</button>
        </div>

        <div class="orderdet-content">
            <div class="non-payment">
                {{status == 0 ? "未付款" : status == 1 ? "待发货" : status == 2 ? "待提货" : status == 3 ? "已提货" : "交易关闭"}}
                <div class="non-right" v-if="status==2">提货单号:
                    <span class="text">{{orderMsg.get_num}}</span>
                </div>
            </div>
            <div class="payer-msg">
                <div class="payer-tip1">
                    <span class="text">收货人：</span>
                    <span class="inputText">{{orderMsg.replace_name}}</span>
                    <span class="inputText">{{orderMsg.replace_phone}}</span>
                </div>
                <div class="payer-tip2">
                    提货地点：{{(orderMsg.store.province || "") + (orderMsg.store.city || "") + (orderMsg.store.area || "") + (orderMsg.store.area_desc || "")}}
                </div>
                <div class="payer-tip3">
                    自提点：{{(orderMsg.store.area || "") + (orderMsg.store.store_name || "") + " " + (orderMsg.store.store_sn || "")}}店 {{orderMsg.store.phone || ""}}
                </div>
            </div>
            <div class="orderDet-list" v-for="(item,i) in orderMsg.goods" :data-index="i">
                <div class="goodsMsg-title" v-if="status==2||item.id==0">商品信息
                    <div class="look-qr" v-if="status==2">
                        <span class="icon" v-if="item.is_take==0&&item.is_refund!=1" @click="previewImg"
                              :data-url="item.take_code"></span>{{item.is_take == 0 ? "待提货" : "已提货"}}
                    </div>
                    <button class="tohome" v-if="status!=2">
                        <span class="icon"></span>去购物
                    </button>
                </div>
                <div class="goods-list">
                    <div class="goods-item">
                        <router-link @click.native="viewTap" tag="div" class="goodsImg-content"
                                     :to="{path:'detail', query: { gid: item.copy_id, shopid:shopid }}">
                            <img :src="config+item.banner"
                                 class="goods-img">
                        </router-link>
                        <div class="goods-msg">
                            <div class="name">{{item.goods_name}}</div>
                            <div class="num">{{item.standard}}</div>
                            <div class="time"> {{item.take_time}} 提货</div>
                            <div class="price">￥
                                <span class="money">{{item.shop_price}}</span>
                                <span class="s-money">￥{{item.predict_price}}</span>
                                <span class="goods-num">x{{item.buy_num}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="payment-msg">
                <div class="payment-tip1">共{{orderMsg.goods_num}}件商品
                    <div class="tip1-text">合计：
                        <span class="text">￥{{orderMsg.total_price}}</span>
                    </div>
                </div>
                <div class="payment-coupon" v-if="orderMsg.coupon_id">优惠券： -{{orderMsg.coupon_amount}}</div>
                <div class="payment-tip2">实付金额：
                    <div class="tip2-text">￥
                        <span class="text">{{orderMsg.order_account}}</span>
                    </div>
                </div>
            </div>
            <div class="order-text">
                <div class="text">订单编号：{{orderMsg.order_sn}}</div>
                <div class="text">下单时间：{{orderMsg.create_time}}</div>
            </div>
        </div>

    </div>
</template>
<script>
    import "../../assets/format"
    export default {
        name: 'orderdet',
        data(){
            return {
                config: this.GLOBAL.config,
                status: 0, //订单状态 0去付款 1待发货 2待提货 3已提货  其他-交易关闭
                shopid: '',
                oid: "",
                orderMsg: {
                    store: {}
                } //订单信息
            }
        },

        created: function () {
            var oid = this.$route.query.oid;
            var shopid = this.GLOBAL.shopMsg.shopid || '';
            this.oid = oid;
            this.shopid = shopid;

            this.getData();
        },
        methods: {
            getData: function () {
                var userMsg = this.GLOBAL.userMsg;
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                this.axios.get('/minapi/orders/orderdesc', {
                    params: {
                        access_token: userMsg.access_token,
                        order_id: this.oid
                    }
                }).then(res => {
                    var datas = res.data;
                    var error_msg = datas.error_msg;
                    pop.hide();
                    if (datas.error_code == 0) {
                        var order = datas.data;
                        for (var i = 0, len = order.goods.length; i < len; i++) {
                            order.goods[i].take_time = new Date(order.goods[i].take_time * 1000).format('MM月dd日');
                        }
                        order.total_price = (order.order_account - order.coupon_amount).toFixed(2);
                        if (order.total_price - 0 <= 0) {
                            order.total_price = 0.01;
                        }
                        this.shopid = this.GLOBAL.shopMsg.shopid || order.store_id;
                        this.orderMsg = order;
                        this.status = order.order_status
                    } else {
                        this.$createDialog({
                            type: 'alert',
                            title: '错误',
                            content: error_msg,
                            onConfirm: () => {
                                this.$router.back(-1);
                            }
                        }).show()
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: '请求错误：orderdesc',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            previewImg: function (e) {
                var urls = this.config + e.target.dataset.url; //图片下标
                window.open(urls);
            },
            viewTap: function (e) {
                var orderMsg = this.orderMsg;
                if (!orderMsg.order_id) {
                    return
                }
                localStorage.setItem('replacename', orderMsg.replace_name);
            },
            deleteOrder: function (e) {
                var userMsg = this.GLOBAL.userMsg;
                var orderMsg = this.orderMsg;
                if (!orderMsg.order_id) {
                    return
                }
                this.$createDialog({
                    type: 'confirm',
                    title: '删除订单',
                    content: '确认删除该订单？',
                    onConfirm: () => {
                        const pop = this.$createToast({
                            time: 10000,
                        });
                        pop.show();
                        this.axios.get('/minapi/orders/delorder', {
                            params: {
                                order_id: orderMsg.order_id,
                                access_token: userMsg? userMsg.access_token : ''
                            }
                        }).then(res => {
                            var datas = res.data;
                            var info = datas.error_msg;
                            pop.hide();
                            if (datas.error_code == 0) {
                                localStorage.setItem('refresh',1);
                                this.$createDialog({
                                    type: 'alert',
                                    title: '删除订单',
                                    content: '删除订单成功',
                                    onConfirm: () => {
                                        this.$router.back(-1);
                                    }
                                }).show()
                            } else {
                                this.info = this.$createToast({
                                    txt: '操作失败'+info,
                                    type: 'txt'
                                })
                                this.info.show();
                            }
                        }).catch(error => {
                            this.toast = this.$createToast({
                                txt: '请求错误：delorder',
                                type: 'txt'
                            })
                            this.toast.show();
                        })
                    },
                    oncancel: () => {
                        this.toast = this.$createToast({
                            txt: ' 取消删除',
                            type: 'txt'
                        })
                        this.toast.show();
                    }
                }).show()

            }
        },
    }
</script>

<style scoped>
    @import url(./static/css/d_orderdet.css);
</style>
