<template>
    <div class="mainc" v-if="shopMsg">
        <div class='site'>
            <div class='s-present'>当前自提点</div>
            <div class='s-pre'>当前</div>
            <div class='present'>
                <div class='p-name'>自提点：{{shopMsg.store_name}}</div>
                <div class='p-distance'>{{shopMsg.address||'----'}}</div>
            </div>
        </div>
        <div class="address-container" ref="bscroll">
            <div class="address-list bscroll-container">
                <div class="address-item" :class="[shopMsg.store_id==item.store_id?'active':'']" v-for="item in list" :data-shopid="item.store_id" @click="choice">
                    <div class="shop-name" :data-shopid="item.store_id">{{item.store_name}}</div>
                    <div class="shop-address" :data-shopid="item.store_id">{{item.address||'----'}}</div>
                    <div class="distance">
                        <span class="now-icon" :data-shopid="item.store_id"></span>{{item.juli}}km
                    </div>
                </div>
                <div class="btm-nomore" v-if="nomore!=2">{{nomore==1?'加载中···':nomore==3?'没有更多记录了···':' '}}</div>
            </div>

        </div>
    </div>
</template>
<script>
    import "../../assets/format"
    import BScroll from 'better-scroll'
    export default {
        name: "addressa",
        data(){
            return {
                config: this.GLOBAL.config,
                shopMsg: {},
                longitude: "",
                latitude: "",
                types: "", //1首页进入选择门店 2个人中心进入新增自提点
                nomore: 2, //1加载中 2加载完成 3没数据了
                page: 1,
                list: []
            }
        },
        created: function () {
            const shopMsg = JSON.parse(localStorage.getItem('shopMsg'));
            this.shopMsg = shopMsg;
            this.types = undefined || 1;
            this.getData();
        },
        mounted() {
            setTimeout(()=>{
                this.initScroll();
            },20)
        },

        methods: {
            getData:function () {
                var obj = this;
                if (obj.nomore != 2) {
                    return
                }
                this.nomore=1;
                const pop = this.$createToast({
                    time: 10000,
                });
                pop.show()
                this.axios.get('/minapi/store/storelist', {
                    params: {
                        longitude: 112.56566,
                        latitude: 37.73605,
                        page: this.page
                    }
                }).then(res => {
                    var datas = res.data;
                    pop.hide();
                    if (datas.error_code == 0) {
                        var datalist = datas.data;
                        var list = this.list;
                        for (var i = 0, len = datalist.length; i < len; i++) {
                            datalist[i].juli = datalist[i].juli.toFixed(2) - 0;
                        }
                        list = list.concat(datalist);
                        this.list=list;
                        if (datalist.length < 10) {
                            this.nomore=3;
                        } else {
                            this.nomore=2;
                            this.page=this.page+1;
                        }
                        if (this.types == 3 && this.page >= 3) {
                            this.nomore=3;
                        }
                    } else {
                        this.nomore=3;
                    }
                }).catch(error => {
                    this.toast = this.$createToast({
                        txt: ' 请求错误：storelist',
                        type: 'txt'
                    })
                    this.toast.show();
                })
            },
            choice:function(e){
                var shopid = e.target.dataset.shopid;
                var userMsg = this.GLOBAL.userMsg;
                if (this.types == 2) { //添加自提点
                    this.$createDialog({
                        type: 'confirm',
                        title: '添加自提点',
                        content: '确认添加该门店为我的自提点？',
                        onConfirm: () => {
                            const pop = this.$createToast({
                                time: 10000,
                            });
                            pop.show();
                            this.axios.get('/minapi/store/addCarry', {
                                params: {
                                    store_id: shopid,
                                    access_token: userMsg? userMsg.access_token : ''
                                }
                            }).then(res => {
                                var datas = res.data;
                                var info = datas.error_msg;
                                pop.hide();
                                if (datas.error_code == 0) {
                                    localStorage.setItem('address',1);
                                    this.info = this.$createToast({
                                        txt: '添加成功',
                                        type: 'txt'
                                    })
                                    this.info.show();
                                    this.$router.back(-1);
                                } else {
                                    this.info = this.$createToast({
                                        txt: info,
                                        type: 'txt'
                                    })
                                    this.info.show();
                                }
                            }).catch(error => {
                                this.toast = this.$createToast({
                                    txt: '请求错误：addCarry',
                                    type: 'txt'
                                })
                                this.toast.show();
                            })
                        }
                    }).show()
                }else{
                    this.$createDialog({
                        type: 'confirm',
                        title: '切换门店',
                        content: '确认切换该门店?',
                        onConfirm: () => {
                            localStorage.setItem('refresh', 3);
                            localStorage.setItem('shopid', shopid);
                            this.$router.push({name:'index'});
                        }
                    }).show()
                }
            },
            initScroll(){
                if(!this.$refs.bscroll){
                    return ;
                }
                this.scroll = new BScroll(this.$refs.bscroll,{
                    click:true,
                    scrollbar:true,
                    pullUpLoad: {
                        threshold: 50
                    }
                });
                this.scroll.on('pullingUp',()=>{
                    this.getData();
                })
            },
        },
        watch:{
            list:{
                handler:function(){
                    this.$nextTick(()=>{
                        if(this.scroll) {
                            this.scroll.finishPullUp();
                            this.scroll.refresh();
                        }
                    })
                },
                deep:true
            }
        },
        computed: {}
    }
</script>
<style scoped>
    @import url(./static/css/d_address.css);
</style>